/**
 *
 */
export interface Navigator extends BaseElement, IView {
	initialize(init: ONavigatorInit): this
}

export interface ONavigatorInit extends OBaseAreaViewInit<IRegPointer<IWspUiEnv>> {

	/**
	 * Construction du gestionnaire des facettes, responsable de construire la requete au server.
	 * Par défaut, un NavigatorFacetText générique sera construit.
	 */
	buildFacets?: (nav: Navigator, fields: ESrcField[]) => Promise<INavigatorFacets>

	/** Redéfinition de la colonne primaire. */
	primaryCol?: GridColTreeDef

	/** Colonnes secondaires à ajouter du grid. */
	secondaryCols?: GridColDef[]

	/** Actions associées aux items sélectionnés (toolbar et menu contextuel). */
	actions?: IAction<IShortDescCtx>[];

	/** Action excutée au double-click ou sur les frappes entrée ou espace. */
	defaultAction?: IAction<ISpaceTreeCtx>

	/** Racourcis claviers à capter sur la sel courante. */
	accelKeyMgr?: AccelKeyMgr<ISpaceTreeCtx>;

	/** Réagira aux infos issues de ce broker (item courant, highlight d'items selon signature) */
	itemHandlingReact?: IInfoBroker

	/** Contenu de la zone principale lorsque la liste est vide. */
	emptyTree?: (navigator: Navigator) => Node

	/** LastDatas transférés à ONavigatorInit.buildFacets() */
	lastDatas?: JLastDatas
}

/** Gestionnaire des facettes et constructeur de la requete. */
export interface INavigatorFacets extends ISpaceTreeReq, ILastDatasBuilder {

	/**
	 * Construction graphique des facettes
	 * - Les facettes doivent être ajoutées à nav.facetRoot
	 * - La construction des facettes DOIT intégrer nav.navigatorBtns (1, voire 2 boutons) dans nav.facetRoot
	 */
	buildUiFacets: (nav: Navigator, lastDatas: JLastDatas | null) => void
}

export class Navigator extends BaseAreaViewAsync<IRegPointer<IWspUiEnv>> implements IInfoConsumer, IGridLineDrawer, ILastDatasBuilder, IFocusActionables<IShortDescCtx>, IAccelKeyMgrPointer<IShortDescCtx> {

	reg: IReg<IWspUiEnv>;
	wsp: Wsp;

	facets: INavigatorFacets;

	facetRoot: HTMLElement;
	navigatorBtns: HTMLElement;
	grid: Grid;
	datas: NavigatorDatas;

	infoBroker: IInfoBroker;

	accelKeyMgr?: AccelKeyMgr<IShortDescCtx>;

	/** Sélection en cours */
	shortDescs: JSrcFieldsTree[] = [];

	get emitter() {return this.grid} //on pointe le noeud focusable.
	get me() {return this} // Astuce pour retrouver le SpaceTree quand l'actionContext a été surchargé.

	/** boutons à afficher dans la focusToolbar. */
	focusActionables?: IActionable<IShortDescCtx>[];

	/** Actions associées aux items sélectionnés (mémoire pour le menuContextuel). */
	actions?: IAction<IShortDescCtx>[];

	emptyTree: (navigator: Navigator) => Node;

	setCurrentItem(srcUri: srcUri | null) {
		this._currentSrcUri = srcUri;
		this.grid.invalidateRows();
		//A voir: != de explorateur, veut-on bouger ce scroll, pas top si on est en train de passer les items en revue ?
		//if (this._currentSrcUri) this.ensureRowVisible(this._currentSrcUri);
	}

	async selectSrcUri(srcUri: srcUri): Promise<boolean> {
		//if (!await this.datas.fetchSrcUri(srcUri)) return false; attention, potentiellement couteux
		const rowKey = this.datas.findRowKeyBySrcUri(srcUri);
		if (rowKey) this.grid.setSelectedRows(this.datas.getOffset(rowKey));
		return rowKey != null;
	}

	async selectSrcUris(srcUris: srcUri[]): Promise<void> {
		//await this.datas.fetchSrcUris(srcUris); attention, potentiellement couteux
		this.grid.setSelectedRows(
			srcUris.map((srcUri) => this.datas.findRowKeyBySrcUri(srcUri))
				.filter((rowKey) => rowKey != null)
				.map((rowKey) => this.datas.getOffset(rowKey))
		);
	}

	async ensureRowVisible(srcUri: srcUri): Promise<boolean> {
		//if (!await this.datas.fetchSrcUri(srcUri)) return false; Non, potentiellement trop couteux.
		const rowKey = this.datas.findRowKeyBySrcUri(srcUri);
		if (rowKey) this.grid.ensureRowVisible(this.datas.getOffset(rowKey));
		return rowKey != null;
	}

	doSearch(): Promise<void> {
		return this.datas.fetchSearchStart(true);
	}

	protected async _initialize(init: ONavigatorInit): Promise<void> {
		super._initialize(init); //init : reg, area, areaContext
		this.wsp = this.reg.env.wsp;
		this.infoBroker = init.itemHandlingReact;
		this.emptyTree = init.emptyTree;

		const fields = this.wsp.getShortDescFields();
		this.facets = init.buildFacets ? await init.buildFacets(this, fields) : new NavigatorFacetText(fields);
		this.datas = new NavigatorDatas(this.reg).initNavDatas(this);

		const sr = this.attachShadow(DOMSH.SHADOWDOM_INIT);
		this._initAndInstallSkin(this.localName, init);

		this.facetRoot = sr.appendChild(<div id="facetRoot"/>);

		this.navigatorBtns = <c-button î={{reg: this.reg, title: "Rafraichir la liste", icon: "/@skin@/commons/icons/refresh.svg", uiContext: "bar"} as OButtonInit} onclick={(ev: MouseEvent) => {this.doSearch()}}/>;

		const colDefs: GridColDef[] = [init.primaryCol || new GridColTreeDef('srcTree')
			.setFlex('1rem', 1, 1).setMinWidth('55px')
			.setCellBuilder(new CellBuilderSrcIconCodeTitle(this.reg, this.wsp.wspMetaUi, false, this.wsp.srcUriItemsSortFn))];
		if (init.secondaryCols) colDefs.push(...init.secondaryCols);

		this.grid = sr.appendChild(new Grid().initialize({
			selType: 'multi',
			columnDefs: colDefs,
			dataHolder: this.datas,
			hideHeaders: true,
			lineDrawer: this.infoBroker ? this : null,
			skinOver: 'wsp-navigator/grid',
			skinScroll: 'scroll/small',
			noResizableCol: true,
			//autoSelOnFocus: 'first', NON pb pour executer des actions (Créer un item, ...) sur la racine.
			emptyBody: (): Node => {
				if (this.datas.lastError === undefined) {
					return <c-msg label="Chargement..." level="info"/>
				} else if (this.datas.lastError === null) {
					return this.emptyTree ? this.emptyTree(this) : <c-msg label="Aucun item ne correspond à vos critères" level="info"/>
				} else {
					return <c-msg label="Accès au serveur impossible" level="error"/>
				}
			},
			defaultAction: init.defaultAction,
			defaultActionCtx: this
		}));

		if (init.secondaryCols) DOM.setAttrBool(this.grid, "data-has-secondary-cols", true);

		//Impl IContextMenuActionsPointer<IShortDescCtx> sur le grid
		Object.defineProperty(this.grid, "ctxMenuActions", {
			get: (): IContextMenuActions<IShortDescCtx> | null => {return {actions: this.actions, actionContext: this, rect: this.grid.getSelRect()}}
		});

		this.grid.addEventListener('grid-select', function (this: IGrid, ev: Event) {
			const spaceTree = DOMSH.findHost<SpaceTree>(this);
			spaceTree.shortDescs = (this.dataHolder as GridDataHolderJsonArray<JSrcFieldsTree>).getSelectedDatas();
			//console.log("spaceTree.shortDescs::::", spaceTree.shortDescs);
			if (this.matches(':focus')) this.dispatchEvent(new CustomEvent<IFocusActionables<IShortDescCtx>>('c-focus-actions', {detail: spaceTree, bubbles: true, composed: true}));
		});

		this.grid.addEventListener('focus', function (this: IGrid, ev: Event) {
			this.dispatchEvent(new CustomEvent<IFocusActionables<IShortDescCtx>>('c-focus-actions', {detail: DOMSH.findHost(this) as SpaceTree, bubbles: true, composed: true}));
		});

		this.grid.linesNode.setAttribute("draggable", "true");
		this.grid.linesNode.addEventListener('dragstart', function (ev: DragEvent) {
			//console.log(ev);
			ITEM.setShortDescTransferToDragSession(DOMSH.findHost<SpaceTree>(DOMSH.findHost<IGrid>(this)), ev, 'build');
		});
		this.grid.linesNode.addEventListener('dragend', ITEM.resetShortDescTransferToDragSession);

		if (init.accelKeyMgr) {
			this.accelKeyMgr = init.accelKeyMgr;
			this.grid.addEventListener('keydown', (ev: KeyboardEvent) => {
				this.accelKeyMgr.handleKeyboardEvent(ev, this);
			});
		}

		let actions = init.actions;
		if (!actions && this.hasAttribute("actions")) actions = this.reg.mergeLists(...BASIS.extractAttr(this, 'actions').split(' '));
		if (actions) {
			actions = ACTION.injectSepByGroup(actions, this.reg.getPref("groupOrder.wsp.shortDesc", ""), this);
			this.actions = actions;
			this.focusActionables = ActionBtn.buildButtons(actions, this, 'bar');
		}

		//
		this.reg.env.place?.eventsMgr.on("wspUriChange", (this._onUriChangeLstn = this.onUriChange.bind(this)));

		this.facets.buildUiFacets(this, init.lastDatas ? init.lastDatas[init.area.getId()] : null);
		this.datas.fetchSearchStart();
	}

	onUriChange(msg: JWspUriChangeMsg, from: WspsLivePlace | IWspXmlHouse | 'local' | 'server') {
		this.datas.onUriChange(msg, from);
	}

	protected _onUriChangeLstn: (msg: JWspUriChangeMsg, from: WspsLivePlace | IWspXmlHouse | 'local' | 'server') => void;

	connectedCallback() {
		super.connectedCallback();
		if (this.infoBroker) {
			this._highlightSgn = null;
			this._assigned = null;
			const req = new InfoReqCurrentItem();
			this.infoBroker.dispatchInfo(req, this);
			this.setCurrentItem(req.srcUri || null);
			this.infoBroker.addConsumer(this);
		}
	}

	diconnectedCallback() {
		if (this.infoBroker) this.infoBroker.removeConsumer(this);
	}

	onViewHidden(closed: boolean) {
		if (closed) this.reg.env.place?.eventsMgr.removeListener("wspUriChange", this._onUriChangeLstn);
	}

	onInfo(info: IInfo): void {
		if (info instanceof InfoCurrentItem) {
			this.setCurrentItem(info.srcUri);
		} else if (info instanceof InfoHighlighItemSgn) {
			this._highlightSgn = info.sgnPattern;
			this._assigned = info.assigned;
			this.grid.invalidateRows();
		}
	}

	/** */
	redrawLine(row: IGridDataRow, line: HTMLElement): void {
		redrawSrcLine(this.wsp, row.rowKey, line, this._currentSrcUri, this._highlightSgn, this._assigned);
	}

	_currentSrcUri: srcUri;
	protected _highlightSgn: RegExp;
	protected _assigned: srcRef;

	buildLastDatas(parentLastDatas: JLastDatas) {
		if (this.facets?.buildLastDatas) {
			const ld = {};
			this.facets.buildLastDatas(ld);
			if (Object.keys(ld).length > 0) parentLastDatas[this.area.getId()] = ld;
		}
	}
}

