class NavigatorDatas extends SpaceTreeDataSearch {
	initNavDatas(nav: Navigator): this {
		this.hideItemFolderContent = true;
		this.initSpTrDataSearch(nav.facets, 30, 50);
		return this;
	}

	onUriChange(msg: JWspUriChangeMsg, from: WspsLivePlace | IWspXmlHouse | 'local' | 'server') {
		if (this.wsp.code !== msg.wspCd) return;
		const node = this.findRowKeyBySrcUri(msg.srcUri);
		if (node) {
			if (this.shouldHideEntry(msg, node)) {
				this.deleteRowKey(node);
			} else {
				this.refreshRowKey(node);
			}
		}
	}
}


export class NavigatorFacetBase implements INavigatorFacets {

	skin: string | string[];

	/** Balise select de la requete. */
	protected select: Element;

	/** 1er criterion de la requete (fils de la balise where). */
	protected itemsExp: SearchItems;

	/** A incrémenter à chaque chgt de facette. */
	stamp: number = 0;

	constructor(fields: ESrcField[]) {
		this.initSkin();
		this.select = JSX.asXml(() => <select>{fields.map((f) => JSX.asXml(() => <column dataKey={f}/>))}</select>);
		this.itemsExp = new SearchItems().excludeTildeRoot().excludeAirItems(false);
	}


	protected initSkin() {}

	getStartReq(max: number): string {
		this.select.setAttribute("max", max.toString());
		this.itemsExp.afterUri = null;
		this.itemsExp.beforeEndFolder = null;
		return this.buildReq();
	}

	getNextReq(afterSrcUri: srcUri, max: number): string {
		this.select.setAttribute("max", max.toString());
		this.itemsExp.afterUri = afterSrcUri;
		this.itemsExp.beforeEndFolder = null;
		return this.buildReq();
	}

	getReplaceFolderReq(folderUri: srcUri, max: number): string {
		this.select.setAttribute("max", max.toString());
		const path = folderUri + '/';
		this.itemsExp.afterUri = path;
		this.itemsExp.beforeEndFolder = path;
		return this.buildReq();
	}

	/** A surcharger pour construire les facettes. */
	buildUiFacets(nav: Navigator, lastDatas: JLastDatas | null) {
		this.initRoot(nav).appendChild(nav.navigatorBtns);
	}

	protected initRoot(nav: Navigator): Node & ParentNode {
		if (this.skin) {
			const root = nav.facetRoot;
			const reg = nav.reg;
			const sr = root.attachShadow(DOMSH.SHADOWDOM_INIT);
			if (Array.isArray(this.skin)) {
				for (const sk of this.skin) reg.installSkin(sk, sr);
			} else {
				reg.installSkin(this.skin, sr);
			}
			return sr;
		}
		return nav.facetRoot;
	}

	/** A surcharger pour construire les critèrions Xml issus des facettes. */
	protected getCriterions(): Element | Element[] | undefined {return undefined}

	protected buildReq() {
		return DOM.ser(JSX.asXml(() => <request>{this.select}
			<where>{this.itemsExp.toDom()}{this.getCriterions()}</where>
		</request>));
	}

	/** A appeler suite à tout changement de widget pour relancer la requete. */
	protected onChange(this: HTMLInputElement, ev: Event) {
		const nav = DOMSH.findFlatParentElt(this, null, (elt): elt is Navigator => elt instanceof Navigator);
		(nav.facets as NavigatorFacetBase).stamp++;
		nav.datas.fetchSearchStart(true);
	}

	getStamp(): any {return this.stamp}

	isSameStamp(stamp: any): boolean {return stamp === this.stamp}
}

export class NavigatorFacetText extends NavigatorFacetBase {

	textInput: HTMLInputElement;

	protected initSkin() {
		this.skin = "wsp-navigator/facetText";
	}

	buildUiFacets(nav: Navigator, lastDatas: JLastDatas | null) {
		this.initRoot(nav).append(
			<div class="h">{this.buildTextInput(nav, lastDatas)}{nav.navigatorBtns}</div>
		);
	}

	buildLastDatas(parentLastDatas: JLastDatas) {
		const txt = this.textInput?.value.trim();
		if (txt) (parentLastDatas as JLDFacetText).txt = txt;
	}

	protected buildTextInput(nav: Navigator, lastDatas: JLDFacetText | null): HTMLElement {
		this.textInput = <input type="search" spellCheck="false" onchange={this.onChange} oninput={this.onChange} value={lastDatas?.txt}/> as HTMLInputElement;
		return <label id="txt">{this.textInput}</label>;
	}

	protected getCriterions(): Element | Element[] | undefined {
		return this.getTextCriterion();
	}

	protected getTextCriterion(): Element | undefined {
		if (!this.textInput) return undefined;
		const v = this.textInput.value.trim().toLowerCase();
		if (!v) return undefined;
		return JSX.asXml(() => <exp type="ItemCodeOrTitle" regexp={`(?i).*${LANG.escape4RegexpFuzzy(v)}.*`}/>)
	}
}

interface JLDFacetText {
	txt?: string
}


