export class NavigatorFacetItState extends NavigatorFacetText {

	showWarning: boolean
	showValid: boolean
	showTextFilter: boolean

	protected errorCheck: HTMLInputElement;
	protected warningCheck: HTMLInputElement;
	protected validCheck: HTMLInputElement;

	initFacet(showWarning: boolean, showValid: boolean, showTextFilter: boolean): this {
		this.showWarning = showWarning;
		this.showValid = showValid;
		this.showTextFilter = showTextFilter;
		return this;
	}

	protected initSkin() {
		this.skin = ["wsp-navigator/facetText", "wsp-navigator/facetItSate"];
	}

	buildUiFacets(nav: Navigator, lastDatas: JLastDatas | null) {
		const root = this.initRoot(nav);
		root.appendChild(<div id="itStatesCtn">{this.buildErrorInput(nav, lastDatas)}{nav.navigatorBtns}</div>);
		if (this.showTextFilter) root.appendChild(this.buildTextInput(nav, lastDatas));
	}

	buildLastDatas(parentLastDatas: JLastDatas) {
		super.buildLastDatas(parentLastDatas);
		if (!this.errorCheck.checked) (parentLastDatas as JLDFacetItSt).err = false;
		if (!this.warningCheck?.checked) (parentLastDatas as JLDFacetItSt).warn = false;
		if (this.validCheck?.checked) (parentLastDatas as JLDFacetItSt).valid = true;
	}

	protected buildErrorInput(nav: Navigator, lastDatas: JLDFacetItSt | null): HTMLElement | undefined {
		this.errorCheck = <input id="error" type="checkbox" onchange={this.onChange} checked={lastDatas?.err === false ? undefined : ""}/> as HTMLInputElement;
		if (!this.showWarning && !this.showValid) {
			this.errorCheck.checked = true;
			return <div id="itState"/>;
		}
		if (this.showWarning) this.warningCheck = <input id="warning" type="checkbox" onchange={this.onChange} checked={lastDatas?.warn === false ? undefined : ""}/> as HTMLInputElement;
		if (this.showValid) this.validCheck = <input id="valid" type="checkbox" onchange={this.onChange} checked={lastDatas?.valid ? "" : undefined}/> as HTMLInputElement;
		const ctn = <div id="itState"><label class="h" id="error">{this.errorCheck}Erreur</label></div>;
		if (this.warningCheck) ctn.appendChild(<label class="h" id="warning">{this.warningCheck}Alerte</label>);
		if (this.validCheck) ctn.appendChild(<label class="h" id="valid">{this.validCheck}Valide</label>);
		return ctn;
	}

	protected getCriterions(): Element | Element[] | undefined {
		let searchCrit: ISearchCrit;
		const min = this.validCheck?.checked ? 1 : this.warningCheck?.checked ? 2 : this.errorCheck.checked ? 3 : 99;
		if (min >= 3) {
			searchCrit = new SearchStatus(min, min);
		} else {
			const max = this.errorCheck.checked ? 3 : this.warningCheck?.checked ? 2 : 1;
			if (min === 1 && max === 3) {
				//cas particulier Error + Valid => exclure wanring
				searchCrit = new SearchOr(new SearchStatus(min, min), new SearchStatus(max, max));
			} else {
				searchCrit = new SearchStatus(min, max);
			}
		}
		return LANG.pushIfDefined([searchCrit.toDom()], this.getTextCriterion());
	}
}

interface JLDFacetItSt {
	err?: boolean
	warn?: boolean
	valid?: boolean
}


