/** Tous les states error, warning, valid + text filter. */
export const FACET_itStates = (nav: Navigator, fields: ESrcField[]) => new NavigatorFacetItState(fields).initFacet(true, true, true);

/** Items en erreur ou warnings, sans text filter. */
export const FACET_itProblems = (nav: Navigator, fields: ESrcField[]) => new NavigatorFacetItState(fields).initFacet(true, false, false);

/** Items en erreur, sans text filter. */
export const FACET_itErrors = (nav: Navigator, fields: ESrcField[]) => new NavigatorFacetItState(fields).initFacet(false, false, false);